@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #0f172a;
  color: #ffffff;
}

.App {
  min-height: 100vh;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #1e293b;
}

::-webkit-scrollbar-thumb {
  background: #475569;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #64748b;
}

/* Glass morphism effect */
.backdrop-blur {
  backdrop-filter: blur(12px);
}

/* Custom animations */
@keyframes pulse-glow {
  0%, 100% {
    box-shadow: 0 0 20px rgba(251, 146, 60, 0.3);
  }
  50% {
    box-shadow: 0 0 40px rgba(251, 146, 60, 0.6);
  }
}

.animate-pulse-glow {
  animation: pulse-glow 2s ease-in-out infinite;
}

/* Button hover effects */
button:hover {
  transform: translateY(-1px);
  transition: all 0.2s ease;
}

/* Card hover effects */
.card-hover:hover {
  transform: translateY(-2px);
  transition: all 0.3s ease;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.4), 0 10px 10px -5px rgba(0, 0, 0, 0.2);
}

/* Status indicators */
.status-active {
  position: relative;
}

.status-active::before {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: linear-gradient(45deg, #22c55e, #16a34a);
  border-radius: 8px;
  z-index: -1;
  animation: pulse-glow 2s ease-in-out infinite;
}

/* Wallet address styling */
.wallet-address {
  font-family: 'JetBrains Mono', 'Fira Code', monospace;
  font-size: 0.75rem;
  background: rgba(30, 41, 59, 0.5);
  padding: 0.5rem;
  border-radius: 4px;
  border: 1px solid rgba(71, 85, 105, 0.3);
}

/* Trade activity animation */
@keyframes trade-flash {
  0% { background-color: rgba(34, 197, 94, 0.2); }
  50% { background-color: rgba(34, 197, 94, 0.4); }
  100% { background-color: rgba(30, 41, 59, 0.3); }
}

.trade-activity {
  animation: trade-flash 1s ease-in-out;
}

/* Loading states */
.loading-shimmer {
  background: linear-gradient(90deg, 
    rgba(71, 85, 105, 0.3) 0%, 
    rgba(71, 85, 105, 0.6) 50%, 
    rgba(71, 85, 105, 0.3) 100%
  );
  background-size: 200% 100%;
  animation: shimmer 1.5s infinite;
}

@keyframes shimmer {
  0% { background-position: -200% 0; }
  100% { background-position: 200% 0; }
}

/* Gradient text effects */
.gradient-text {
  background: linear-gradient(135deg, #f97316, #ea580c);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Custom focus states */
input:focus, textarea:focus, button:focus {
  outline: 2px solid rgba(251, 146, 60, 0.5);
  outline-offset: 2px;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .container {
    padding: 1rem;
  }
  
  .grid-responsive {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
}

/* Dark theme overrides for shadcn components */
.dark {
  --background: 15 23 42;
  --foreground: 248 250 252;
  --card: 30 41 59;
  --card-foreground: 248 250 252;
  --popover: 30 41 59;
  --popover-foreground: 248 250 252;
  --primary: 251 146 60;
  --primary-foreground: 15 23 42;
  --secondary: 71 85 105;
  --secondary-foreground: 248 250 252;
  --muted: 71 85 105;
  --muted-foreground: 148 163 184;
  --accent: 71 85 105;
  --accent-foreground: 248 250 252;
  --destructive: 239 68 68;
  --destructive-foreground: 248 250 252;
  --border: 71 85 105;
  --input: 71 85 105;
  --ring: 251 146 60;
}